import React from 'react';
import { hashHistory } from 'dva/router';
import { Button, Row, Input } from 'antd';
import { config } from 'utils';

const Lock = ( ) => {

  const handleUnlock = () => {
    hashHistory.push('/')
  }

  return (
    <div>
        <div className="background"></div>
        <div className="card">
            <div className="logo">
              <img alt={'logo'} src={config.whiteLogoSrc} />
              <span>Bear</span>
            </div>
            <Row style={{ textAlign: 'center' }}>
              <Input size="large" placeholder="" type="password" />
              <Button type="primary" size="large" onClick={handleUnlock}></Button>
            </Row>
          </div>
    </div>
  )
}

export default Lock;



